export function getAllFeatures(): MarketingFeatureContent[] {
	return FEATURE_CONTENT;
}

export function getFeatureBySlug(
	slug: string,
): MarketingFeatureContent | undefined {
	return FEATURE_CONTENT.find((feature) => feature.slug === slug);
}
